import React from 'react'
import Navbar1 from "../components/Navbar1";
import Hero from '../components/Hero';
import HomeImg from '../assets/pumkinHero.png';
import Destination from '../components/Destination';

import Footer from '../components/Footer';
import Logo from '../assets/pumpking.png';

function Home() {
  return (
    <div>
         <Navbar1 />
         <Hero
         cName="hero"
         heroImg={HomeImg}
         logo={Logo} 
         title="Your shopping paradise!"
         text="Choose Your Favourite Things."
         buttonText="Trending Now"
         url="/"
         btnClass="show"
         />
         <Destination/>
         
         <Footer/>
    </div>
  )
}

export default Home
