import React from 'react'
import Navbar1 from "../components/Navbar1";
import Hero from '../components/Hero';
import ServiceImg from '../assets/pumkingServices.png';
import Footer from '../components/Footer';
import Trip from '../components/Trip';
import Logo from '../assets/pumpking.png';
import Carousel from '../components/MyCarousel';
import Dropdown from '../components/ServicesDropdown';

function Service() {
  return (
    <div>
      <Navbar1 />
         <Hero
         cName="hero-mid"
         heroImg={ServiceImg}
         logo={Logo} 
         title="Our Services"
        
         btnClass="hide"
         />
          <br/>
         
        <Carousel/>
        <br/>
        <Dropdown/>
         <Footer/>
    </div>
  )
}

export default Service