import { Component } from "react";
import Mountain1 from "../assets/Event2.jpg";
import Mountain2 from "../assets/Event1.jpg";
import Mountain3 from "../assets/Event5.jpg";
import Mountain4 from "../assets/Event3.jpg";
import "./DestinationStyles.css";
import Table from "react-bootstrap/Table";
import { Container } from "react-bootstrap";

class EventsDescription extends Component {
  render() {
    return (
      <div className={this.props.className}>
        <div className="row mt-4 p-4">
          <div className="des-text col-12 col-md-6 col-lg-6 mt-2">
            <h1> Our Annual DJ Party</h1>
            <p>
              Yesterday, our city came alive with the rhythmic beats and vibrant
              melodies of the annual music festival. The air was filled with an
              electric energy as music enthusiasts gathered to celebrate a day
              of pure musical magic. From local indie bands to chart-topping
              headliners, the festival showcased a diverse lineup that catered
              to every musical taste. Under the clear skies, attendees reveled
              in the infectious tunes that echoed through the festival grounds.
              The stages were alive with dynamic performances, and the crowd
              danced freely, creating an atmosphere of shared joy and
              camaraderie. <br />
              Food trucks lined the venue, offering a variety of treats to keep
              the energy high, and vibrant stalls adorned with colorful lights
              added to the festive ambiance. As the sun dipped below the
              horizon, the festival transformed into a kaleidoscope of lights,
              with the stages illuminated and the crowd swaying to the rhythm.{" "}
              <br />
              It was a day where music transcended boundaries, bringing people
              together to share in the universal language of melody and rhythm.
              Memories were made, friendships were forged, and for a fleeting
              moment, time stood still as the music festival on yesterday became
              a treasured chapter in the collective memory of our city's vibrant
              cultural tapestry.
            </p>
          </div>

          <div className="image col-12 col-md-6 col-lg-6">
            <div className="row mt-4">
              <div className="col-12 col-md-12 col-lg-12 mt-5">
                <img alt="img" src={Mountain1} />
              </div>
              {/* <div className="col-6 col-md-6 col-lg-6 ">
                <img alt="img" src={Mountain2} />
              </div> */}
            </div>
          </div>
        </div>

        <div className=" row mt-5 p-4">
          <h2 className="secondH2"></h2>
          <div className=" image col-12 col-md-6 col-lg-6">
            <div className="row mt-4">
              <div className="col-6 col-md-6 col-lg-6 mt-5">
                <img alt="img" src={Mountain3} />
              </div>
              <div className="col-6 col-md-6 col-lg-6 ">
                <img alt="img" src={Mountain4} />
              </div>
            </div>
          </div>

          <div className="des-text col-12 col-md-6 col-lg-6 mt-2">
            <h1> Our Annual Cricket Match</h1>
            <p>
              Every year, our community eagerly anticipates the exhilarating
              Annual Cricket Match, a tradition that brings together players and
              spectators alike in the spirit of friendly competition and
              camaraderie. The cricket field buzzes with excitement as teams
              gear up for the much-anticipated clash, each player fueled by a
              passion for the game and a desire to make their mark on the pitch.
              Families and friends gather around the field, setting up picnic
              blankets and cheering on their favorite teams.
              <br /> The atmosphere is festive, with the aroma of popcorn and
              the sounds of laughter filling the air. Children, wide-eyed with
              enthusiasm, imitate their cricketing heroes as they watch the
              action unfold. The match itself is a spectacle of skill and
              sportsmanship, with players showcasing their best moves and
              strategic plays. The crack of the bat and the cheers of the crowd
              create a symphony of sounds that echo the joyous spirit of the
              occasion. <br />
              Whether you're a cricket aficionado or a casual observer, the
              Annual Cricket Match is a time-honored tradition that unites the
              community in the love of the game. As the sun sets on the cricket
              field, memories are made, and the bonds of friendship are
              strengthened. Win or lose, the Annual Cricket Match is more than
              just a game; it's a celebration of unity, teamwork, and the
              enduring spirit of sportsmanship that defines our community year
              after year.
            </p>
          </div>
        </div>
        <br/>
        <h1>Upcoming Events</h1>

      <Container>
        <div >
          <Table striped bordered hover > 
            <thead>
              <tr>
                <th>#</th>
                <th>Events</th>
                <th>Months</th>
                
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>1</td>
                <td>Football Match</td>
                <td>March</td>
                
              </tr>
              <tr>
                <td>2</td>
                <td>Street Food festival</td>
                <td>July</td>
                
              </tr>
              <tr>
                <td>3</td>
                <td>DJ Party</td>
                <td>December</td>
                
              </tr>
             
            </tbody>
          </Table>
        </div>
        <br/>
        </Container>
      </div>
    );
  }
}
export default EventsDescription;
