import React from 'react'
import Navbar1 from "../components/Navbar1";
import Hero from '../components/Hero';
import AboutImg from '../assets/contactPumking.jpg';
import Footer from '../components/Footer';
import Logo from '../assets/pumpking.png';
import ContactTextFeels from '../components/ContactTextFeels';

function Contact() {
  return (
    <div>
       <Navbar1 />
         <Hero
         cName="hero-mid"
         heroImg={AboutImg}
         logo={Logo}
         title="Contact"
        
         btnClass="hide"
         />
         <ContactTextFeels/>
         <Footer/>
    </div>
  )
}

export default Contact