import { Component } from "react";
import Mountain1 from "../assets/pumkingface.jpeg";
import Mountain2 from "../assets/pumkingEntrance.jpeg";
import Mountain3 from "../assets/pumkingDestination.png";
import Mountain4 from "../assets/pumkingDestination2.jpeg";
import "./DestinationStyles.css";

class DestinationData extends Component {
  render() {
    return (
      <div className={this.props.className}>
        <div className="row mt-4 p-4">
          <div className="des-text col-12 col-md-6 col-lg-6 mt-2">
            <h2>Welcome to Curcubita shopping Mall</h2>
            <p>
              Welcome to Metro Haven Mall – where luxury meets convenience in an
              unparalleled shopping experience! Nestled in the heart of the
              city, Curcubita shopping Mall is a sprawling oasis of style,
              sophistication, and sheer indulgence. As you step through our
              grand entrance, you'll be transported into a world of endless
              possibilities and unparalleled excitement.<br/><br/> The architecture of
              Curcubita shopping Mall is a fusion of modern elegance and
              timeless charm, featuring sleek glass facades and cutting-edge
              design that seamlessly blend with classic elements. The moment you
              walk in, you're greeted by a stunning atrium bathed in natural
              light, creating a vibrant atmosphere that sets the stage for an
              extraordinary shopping adventure.
            </p>
          </div>

          <div className="image col-12 col-md-6 col-lg-6">
            <div className="row mt-4">
              <div className="col-6 col-md-6 col-lg-6 mt-5">
                <img alt="img" src={Mountain1} />
              </div>
              <div className="col-6 col-md-6 col-lg-6 ">
                <img alt="img" src={Mountain2} />
              </div>
            </div>
          </div>
        </div>

        <div className=" row mt-5 p-4">
          <h2 className="secondH2"></h2>
          <div className=" image col-12 col-md-6 col-lg-6">
            <div className="row mt-4">
              <div className="col-6 col-md-6 col-lg-6 mt-5">
                <img alt="img" src={Mountain3} />
              </div>
              <div className="col-6 col-md-6 col-lg-6 ">
                <img alt="img" src={Mountain4} />
              </div>
            </div>
          </div>

          <div className="des-text col-12 col-md-6 col-lg-6 mt-2">
            <p>
              Dive into a retail wonderland, where the latest trends and top
              brands converge in a harmonious symphony of fashion, technology,
              and lifestyle. Our diverse range of stores caters to every taste
              and preference, ensuring that every shopper finds something to
              suit their style. From high-end fashion boutiques to tech-savvy
              gadget emporiums, Curcubita shopping Mall is a treasure trove for
              those with an eye for quality and sophistication.
              <br/>
              <br/>
               For those seeking a culinary journey, our food court is a gastronomic haven.
              Indulge in a diverse array of international cuisines, savor
              artisanal dishes, and treat your taste buds to an unforgettable
              experience. Whether you're in the mood for a quick bite or a
              leisurely gourmet meal, our dining options cater to all palates.
            </p>
          </div>
        </div>
      </div>
    );
  }
}
export default DestinationData;
