import React from "react";
import { Container } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Drinks from "../assets/pumpkinMilkshake.jpeg";
import Mountain5 from "../assets/pumpkinApple.jpeg";
import Mountain6 from "../assets/pumpkinSmoothie.jpeg";
import Mountain7 from "../assets/pumpkin-spice-latte.jpeg";
import Mountain8 from "../assets/beauty1.jpg";
import Mountain9 from "../assets/beauty2.jpg";
import Mountain10 from "../assets/beauty3.jpg";
import Mountain11 from "../assets/beauty4.jpg";
import "./TripStyle.css";

function Trip() {
  return (
    <div className="row p-3 ">
      <h1 className="header">Sample Products</h1>
      <p></p>
      <h1 className="categay"> Pumpkin Soft Drinks</h1>

      <div className="col-12 col-md-6 col-lg-3">
        <Container>
          <div className="p-4 ">
            <Card
              className="fullCard p-1"
              style={{ width: "20rem", height: "30rem" }}
            >
              <div className="t-image">
                <Card.Img variant="top" src={Drinks} />
              </div>

              <Card.Body>
                <Card.Title className="cardTitle">Pumpkin Milkshake</Card.Title>
                <Card.Text className="pera ">
                  If your Thanksgiving dinner left you with just a little too
                  much extra pie, we've got good news! You can re-use that
                  pumpkin pie in a whole new way with this easy pumpkin pie
                  milkshake.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Container>
      </div>

      <div className="col-12 col-md-6 col-lg-3">
        <Container>
          <div className="p-4 ">
            <Card
              className="fullCard p-1"
              style={{ width: "20rem", height: "30rem" }}
            >
              <div className="t-image">
                <Card.Img variant="top" src={Mountain5} />
              </div>
              <Card.Body>
                <Card.Title className="cardTitle">
                  Pumpkin Apple Punch
                </Card.Title>
                <Card.Text className="pera">
                  You only need three ingredients to make this fall-inspired
                  punch, but if you want to take it to the next level, try out
                  the homemade chocolate caramel sauce for the glass.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Container>
      </div>

      <div className="col-12 col-md-6 col-lg-3">
        <Container>
          <div className="p-4 ">
            <Card
              className="fullCard p-1"
              style={{ width: "20rem", height: "30rem" }}
            >
              <div className="t-image">
                <Card.Img variant="top" src={Mountain6} />
              </div>
              <Card.Body>
                <Card.Title className="cardTitle">Pumpkin Smoothie</Card.Title>
                <Card.Text className="pera">
                  Using pumpkin pie filling that’s been frozen before blending
                  gives this smoothie a built-in sweetness that we love.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Container>
      </div>

      <div className="col-12 col-md-6 col-lg-3">
        <Container>
          <div className="p-4 ">
            <Card
              className="fullCard p-1"
              style={{ width: "20rem", height: "30rem" }}
            >
              <div className="t-image">
                <Card.Img variant="top" src={Mountain7} />
              </div>
              <Card.Body>
                <Card.Title className="cardTitle">
                  Pumpkin Spice Latte
                </Card.Title>
                <Card.Text className="pera">
                  You can be your own barista with this recipe for a classic
                  PSL. And the best part is that it takes just five minutes to
                  make! Mornings have never been cozier.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Container>
      </div>

      <h1 className="categay">Pumpkin Beauty Products</h1>

      <div className="col-12 col-md-6 col-lg-3">
        <Container>
          <div className="p-4 ">
            <Card
              className="fullCard p-1"
              style={{ width: "20rem", height: "30rem" }}
            >
              <div className="t-image">
                <Card.Img variant="top" src={Mountain8} />
              </div>
              <Card.Body>
                <Card.Title className="cardTitle">
                  Burt's Bees Pumpkin Spice Moisturizing Lip Balm
                </Card.Title>
                <Card.Text className="pera">
                  The change in seasons can wreak havoc on your lips, but this
                  Burt's Bees pumpkin lip balm glides on effortlessly to deliver
                  a surge of much-needed hydration.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Container>
      </div>

      <div className="col-12 col-md-6 col-lg-3">
        <Container>
          <div className="p-4 ">
            <Card
              className="fullCard p-1"
              style={{ width: "20rem", height: "30rem" }}
            >
              <div className="t-image">
                <Card.Img variant="top" src={Mountain10} />
              </div>
              <Card.Body>
                <Card.Title className="cardTitle">
                  Tree Hut Pumpkin Spice Latte Shea Sugar Scrub
                </Card.Title>
                <Card.Text className="pera">
                  The Pumpkin Spice Latte version is the perfect addition to
                  your routine because sugar, a super-gentle exfoliant, removes
                  the driest layer of dead skin cells to reveal smooth-AF skin
                  underneath.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Container>
      </div>

      <div className="col-12 col-md-6 col-lg-3">
        <Container>
          <div className="p-4 ">
            <Card
              className="fullCard p-1"
              style={{ width: "20rem", height: "30rem" }}
            >
              <div className="t-image">
                <Card.Img variant="top" src={Mountain9} />
              </div>
              <Card.Body>
                <Card.Title className="cardTitle">Eminence Yam</Card.Title>
                <Card.Text className="pera">
                  Chemical peels are one of the best ways to help reverse your
                  skin concerns, like fading dark spots and fine lines. In
                  addition to pumpkin, this chemical peel includes yam, which is
                  rich in beta-carotene and vitamins to slow the signs of aging.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Container>
      </div>

      <div className="col-12 col-md-6 col-lg-3">
        <Container>
          <div className="p-4 ">
            <Card
              className="fullCard p-1"
              style={{ width: "20rem", height: "30rem" }}
            >
              <div className="t-image">
                <Card.Img variant="top" src={Mountain11} />
              </div>
              <Card.Body>
                <Card.Title className="cardTitle">
                  ELEMIS Superfood AHA Glow Cleansing Butter
                </Card.Title>
                <Card.Text className="pera">
                  Remove makeup, and nourish your skin with the help of
                  DeRosa’s go-to pumpkin beauty product from ELEMIS. It’s a
                  standout for DeRosa because it contains fermented pumpkin,
                   and rice butter to leave your skin healthy and glowy.
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
        </Container>
      </div>
    </div>
  );
}

export default Trip;
