import { Component } from "react";
import Mountain1 from "../assets/blogpage1.jpg";
import Mountain2 from "../assets/blogPage5.jpg";
import Mountain3 from "../assets/blogPage2.jpg";
import Mountain4 from "../assets/blogPage3.jpg";
import "./BlogsPageStyle.css";
import { Container } from "react-bootstrap";

class DestinationData extends Component {
  render() {
    return (
      <div className={this.props.className}>
        <Container>
          <div className="row mt-4 p-4 fulldiv">
            <div className="des-text col-12 col-md-6 col-lg-6 mt-2">
              <h2>CELEBRATING PUMPKIN FESTIVAL...</h2>
              <p>
                A celebrated pumpkin festival is a lively event dedicated to the
                joyous spirit of autumn and the versatile pumpkin. Typically
                held in communities around the world, these festivals feature a
                vibrant array of activities, including pumpkin carving contests,
                creative displays of elaborately carved pumpkins, pumpkin-themed
                food and beverages, live music, and entertainment. <br/>Families
                often gather to enjoy hayrides, corn mazes, and various games
                set against the backdrop of colorful autumn foliage.<br/> Local
                artisans and vendors may showcase pumpkin-related crafts and
                products. Some festivals incorporate agricultural elements, such
                as pumpkin picking and farm tours.<br/> The atmosphere is festive,
                fostering a sense of community and appreciation for the harvest
                season. Whether held in small towns or larger cities, celebrated
                pumpkin festivals bring people together to revel in the beauty,
                creativity, and culinary delights associated with this iconic
                autumnal gourd.
              </p>
            </div>

            <div className="image col-12 col-md-6 col-lg-6">
              <div className="row mt-4">
                <div className="col-12 col-md-12 col-lg-12 mt-5">
                  <img alt="img" src={Mountain1} />
                </div>
              </div>
            </div>
          </div>

          <div className=" row mt-5 p-4 fulldiv">
            <h2 className="secondH2">CELEBRATING PUMPKIN FESTIVAL...</h2>
            <div className=" image col-12 col-md-6 col-lg-6">
              <div className="row mt-4">
                <div className="col-6 col-md-6 col-lg-6 mt-5">
                  <img alt="img" src={Mountain3} />
                </div>
                <div className="col-6 col-md-6 col-lg-6 ">
                  <img alt="img" src={Mountain4} />
                </div>
              </div>
            </div>

            <div className="des-text col-12 col-md-6 col-lg-6 mt-2">
              <p>
                The importance of a pumpkin festival lies in its ability to
                bring communities together, celebrate the harvest season, and
                highlight the cultural and agricultural significance of
                pumpkins. These festivals play several crucial roles: A
                celebrated pumpkin festival is a lively event dedicated to the
                joyous spirit of autumn and the versatile pumpkin. <br/>Typically
                held in communities around the world, these festivals feature a
                vibrant array of activities, including pumpkin carving contests,
                creative displays of elaborately carved pumpkins, pumpkin-themed
                food and beverages, live music, and entertainment. Families
                often gather to enjoy hayrides, corn mazes, and various games
                set against the backdrop of colorful autumn foliage. <br/>Local
                artisans and vendors may showcase pumpkin-related crafts and
                products. Some festivals incorporate agricultural elements, such
                as pumpkin picking and farm tours. The atmosphere is festive,
                fostering a sense of community and appreciation for the harvest
                season. Whether held in small towns or larger cities, celebrated
                pumpkin festivals bring people together to revel in the beauty,
                creativity, and culinary delights associated with this iconic
                autumnal gourd.
              </p>
            </div>
          </div>

          <div className="row mt-4 p-4 fulldiv">
            <div className="des-text col-12 col-md-6 col-lg-6 mt-2">
              <h2>IMPORTANCE OF PUMPKIN</h2>
              <p>
                Pumpkins hold a multifaceted importance deeply rooted in
                culinary, cultural, and agricultural realms. In the kitchen,
                their culinary versatility shines, offering a rich source of
                nutrients, including vitamins A and C, potassium, and fiber.
                Beyond their nutritional value, pumpkins symbolize the fall
                harvest, playing a central role in cultural celebrations like
                Halloween and Thanksgiving.<br/> Agriculturally, they are a vital
                crop, known for their adaptability and contribution to
                sustainable farming practices. Economically, the pumpkin
                industry generates revenue, supporting farmers, creating jobs,
                and contributing to diverse product lines. <br/> Additionally,
                pumpkins serve as seasonal decorations, adding warmth and
                festivity to autumn environments. Their environmentally friendly
                nature, as biodegradable entities and contributors to
                composting, further underscores their importance in promoting
                sustainable practices. In essence, the pumpkin's significance
                transcends its culinary appeal, embodying cultural traditions,
                sustaining agriculture, and fostering a sense of seasonal joy.
              </p>
            </div>

            <div className="image col-12 col-md-6 col-lg-6">
              <div className="row mt-4">
                <div className="col-12 col-md-12 col-lg-12 mt-5">
                  <img alt="img" src={Mountain2} />
                </div>
              </div>
            </div>
          </div>
        </Container>
      </div>
    );
  }
}
export default DestinationData;
