import React from 'react'
import Navbar1 from "../components/Navbar1";
import Hero from '../components/Hero';
import ProductImg from '../assets/EventsHero.jpg';
import Footer from '../components/Footer';
import CarousalEvents from '../components/EventsCarousel';
import Logo from '../assets/pumpking.png';
import EventsDescription from '../components/EventsDescription';


function Events() {
  return (
    <div>
      <Navbar1 />
         <Hero
         cName="hero-mid"
         heroImg={ProductImg}
         logo={Logo}
         title="Our Events"
        
         btnClass="hide"
         />

         <CarousalEvents/>

         <EventsDescription/>
         
         <Footer/>
    </div>
  )
}

export default Events