import React from 'react'
import "./HeroStyles.css";
import {motion} from 'framer-motion'

function Hero(props) {
  return (
    <div className={props.cName}>
        <img alt='HerpImg' src={props.heroImg}/>
        <motion.div 
        className='hero-text'
         initial={{
          y:-550,
        }}
        animate={{
          y:0,
        }}
        transition={{
          delay:1,
          duration:0.4,
          type:'spring',
          stiffness:100
        }}
        
        >
          <div className='logoname'>
          <img alt='img' src={props.logo} />
          </div>
         
            <h1 >{props.title}</h1>
            <p>{props.text}</p>
            <a href={props.url} className={props.btnClass}>
              {props.buttonText}
            </a>
        </motion.div>

    </div>
  )
}

export default Hero