import React from 'react'
import Navbar1 from "../components/Navbar1";
import Hero from '../components/Hero';
import ProductImg from '../assets/night.jpg';
import Footer from '../components/Footer';

import Logo from '../assets/pumpking.png';
import BlogesPage from '../components/BlogsPage';


function Blog() {
  return (
    <div>
      <Navbar1 />
         <Hero
         cName="hero-mid"
         heroImg={ProductImg}
         logo={Logo}
         title="Our Blogs"
        
         btnClass="hide"
         />

     
         <BlogesPage/>
         
         
         <Footer/>
    </div>
  )
}

export default Blog