import React from 'react'
import { Container } from 'react-bootstrap';
import Carousel from 'react-bootstrap/Carousel';
import "./EventsCarouselStyle.css";




export default function Home() {
  
  return (
    <div className='m-4'>
      <br/>
      <Container>

      <Carousel fade>
      <Carousel.Item interval={1000}>
        <img src='images/Event3.jpg'
          className=' w-90 size' />
        <Carousel.Caption>
          <h3 className='text'>Cricket Matchs</h3>
          <p></p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item interval={1000}>
        <img src='images/Event2.jpg'
         className=' w-90 size'/>
        <Carousel.Caption>
          <h3 className='text'>DJ Dance Events</h3>
          <p></p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item interval={1000}>
        <img src='images/Event4.jpg'
         className=' w-90 size'/>
        <Carousel.Caption>
          <h3 className='text'>Street Food Events</h3>
          <p>
          
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>

    

    </Container>
       
    </div>
  )
}
