import React from "react";
import { Container } from "react-bootstrap";
import "./AboutUsStyle.css";
import { motion } from "framer-motion";

function AboutUs() {
  return (
    <div className="about-container">
      <div className="row">
        <h1>Our History</h1>
        <motion.p>
          Curcubita Shopping Mall was born out of a visionary idea to redefine
          the retail landscape and elevate the shopping experience to
          unprecedented heights. Established in [2010], our journey began with a
          commitment to create a space that transcends the ordinary, blending
          commerce, culture, and community.
          <br />
          In the early stages, Curcubita set out to acquire a prime location
          that would serve as the canvas for our ambitious venture. The
          founders, driven by a passion for innovation and a deep understanding
          of consumer needs, meticulously designed the mall's blueprint to
          reflect a harmonious fusion of modernity and timelessness.
          <br />
          Construction kicked off in [2011], with a dedicated team of architects
          and designers working tirelessly to bring the vision to life. The
          result is a architectural masterpiece, characterized by sleek lines,
          spacious interiors, and a thoughtful layout that seamlessly integrates
          various retail sectors.
        </motion.p>
      </div>

      <div className="row">
        <h1>Our Mission</h1>
        <p>
          "At Curcubita Shopping Mall, our mission is to create a vibrant and
          inclusive shopping destination that transcends traditional retail
          boundaries. We are committed to providing a diverse and exceptional
          shopping experience, fostering a sense of community, and promoting
          sustainability. Through continuous innovation and customer-centric
          approaches, we strive to be the preferred choice for shoppers seeking
          quality, variety, and a welcoming atmosphere."
        </p>
      </div>

      <div className="row">
        <h1>Our Vision</h1>
        <p>
          "At Curcubita Shopping Mall, our mission is to create a vibrant and
          inclusive shopping destination that transcends traditional retail
          boundaries. We are committed to providing a diverse and exceptional
          shopping experience, fostering a sense of community, and promoting
          sustainability. Through continuous innovation and customer-centric
          approaches, we strive to be the preferred choice for shoppers seeking
          quality, variety, and a welcoming atmosphere."
        </p>
      </div>
    </div>
  );
}

export default AboutUs;
