import React from 'react'
import Navbar1 from "../components/Navbar1";
import Hero from '../components/Hero';
import ProductImg from '../assets/pumpkinDrinks.jpg';
import Footer from '../components/Footer';
import Trip from '../components/Trip';
import Logo from '../assets/pumpking.png';


function Products() {
  return (
    <div>
      <Navbar1 />
         <Hero
         cName="hero-mid"
         heroImg={ProductImg}
         logo={Logo}
         title="Our Products"
        
         btnClass="hide"
         />

         <Trip/>
         
         <Footer/>
    </div>
  )
}

export default Products