import React from 'react'
import { Container } from 'react-bootstrap';
import Carousel from 'react-bootstrap/Carousel';
import "./MyCarouselStyle.css";




export default function Home() {
  
  return (
    <div className='m-4'>
      <br/>
      <Container>

      <Carousel fade>
      <Carousel.Item interval={1000}>
        <img src='images/freeWifi.jpg'
          className=' w-90 size' />
        <Carousel.Caption>
          <h3 className='text'>Free Wifi</h3>
          <p></p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item interval={1000}>
        <img src='images/GreenInitiatives.jpg'
         className=' w-90 size'/>
        <Carousel.Caption>
          <h3 className='text'>Green Initiatives</h3>
          <p></p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item interval={1000}>
        <img src='images/ParkingFacilities.jpg'
         className=' w-90 size'/>
        <Carousel.Caption>
          <h3 className='text'>Parking Facilities</h3>
          <p>
          
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>

    

    </Container>
       
    </div>
  )
}
