import React from "react";
import Accordion from "react-bootstrap/Accordion";
import "./ServicesDropdownStyle.css";
import { Container } from "react-bootstrap";

function AllCollapseExample() {
  return (
    <Accordion>
      <Container className="fullbox">
        <Accordion.Item eventKey="0" className="box ">
          <Accordion.Header>Personal Shopping Services</Accordion.Header>
          <Accordion.Body>
            Experience luxury with our personalized shopping services at
            Curcubita. Our skilled and knowledgeable stylists are here to assist
            you in curating the perfect wardrobe or selecting gifts for any
            occasion. Enjoy a tailored shopping experience that caters to your
            unique style and preferences
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="1">
          <Accordion.Header>Dining and Culinary Experiences</Accordion.Header>
          <Accordion.Body>
            Indulge in a culinary journey with various dining options, including
            casual cafes, fine dining restaurants, and international cuisine.
            The food court and restaurant spaces cater to every palate.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="2">
          <Accordion.Header>Culinary Extravaganza</Accordion.Header>
          <Accordion.Body>
            Embark on a culinary journey at Curcubita's dining precinct. Our
            diverse range of dining options ensures that every meal is an
            experience. From the aromatic blends of international cuisines to
            the cozy ambiance of our cafes, indulge in exquisite flavors that
            cater to a variety of tastes and preferences.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="3">
          <Accordion.Header>Eventful Calendar of Activities</Accordion.Header>
          <Accordion.Body>
            Discover more than just shopping at Curcubita. Our event calendar is
            brimming with exciting activities, from fashion shows that showcase
            the latest trends to live performances that captivate audiences.
            Join us for exclusive product launches and promotions, creating
            memorable moments during your visit.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="4">
          <Accordion.Header>Customer Service Desk</Accordion.Header>
          <Accordion.Body>
            Friendly and knowledgeable staff at the customer service desk are
            ready to assist you with inquiries, provide information, and enhance
            your overall shopping experience.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="5" className="box">
          <Accordion.Header>Family-Friendly Play Zone</Accordion.Header>
          <Accordion.Body>
            Families with little ones can relax and enjoy their shopping
            experience knowing that our dedicated play zone provides a safe and
            entertaining space for children. Filled with engaging activities,
            it's a haven for fun and laughter, ensuring a delightful visit for
            the whole family.
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="6" className="box">
          <Accordion.Header>Free Wi-Fi</Accordion.Header>
          <Accordion.Body>
            Stay connected while you shop with complimentary Wi-Fi throughout
            the mall, allowing you to share your experiences, check product
            reviews, or stay in touch with friends and family.
          </Accordion.Body>
        </Accordion.Item>
      </Container>
    </Accordion>
  );
}

export default AllCollapseExample;
