import React from "react";
import LifeCicle from "../assets/PumpkinLife.jpg";

import "./DestinationStyles.css";
import DestinationData from "./DestinationData";
import { Container } from "react-bootstrap";

function Destination() {
  return (
    <div className="destination">
      <h1>Curcubita Shoping mall</h1>

      <p>One of the World's best Shopping mall..</p>

      <DestinationData />

      <div className="row mt-5">
        <div className="col-12 life">
          <img alt="img" src={LifeCicle} />
        </div>
        <div className="col-12">
          <Container>
            <br />
            <h1>Movements: Life Cycle of a Pumpkin</h1>
            <div className="sm-col-12 md-col-6 lg-col-6 cycle">
              <h3>1. Seed Germination</h3>
              <p>
                The life cycle begins with a pumpkin seed, which is typically
                planted in the spring when the soil has warmed up. <br />
                The seed absorbs water and swells, initiating the germination
                process.
              </p>
              <h3>2. Seedling Stage</h3>
              <p>
                As the seed germinates, a small shoot emerges from the soil,
                forming the seedling.
                <br /> The seedling develops leaves and begins to
                photosynthesize, relying on sunlight for energy.
              </p>
              <h3>3. Vegetative Growth</h3>
              <p>
                The pumpkin plant continues to grow and produce more leaves,
                stems, and roots during this phase.
                <br /> The plant focuses on establishing a strong root system
                and developing a robust vegetative structure.
              </p>
              <h3>4. Flowering Stage</h3>
              <p>
                As the plant matures, it produces male and female flowers.
                <br /> Bees and other pollinators play a crucial role in
                transferring pollen from male to female flowers, facilitating
                fertilization.
              </p>
              <h3>5. Fruit Development</h3>
              <p>
                Once fertilized, the female flower starts to develop into a
                pumpkin.
                <br /> The ovary of the flower swells and forms the
                characteristic shape of the pumpkin.
                <br /> The plant directs its energy towards the growth and
                maturation of the fruit.
              </p>
              <h3>6. Ripening</h3>
              <p>
                The green pumpkin gradually changes color, indicating the
                ripening process.
                <br /> During this stage, the pumpkin accumulates sugars and
                develops its characteristic orange hue.
              </p>
              <h3>7. Harvesting</h3>
              <p>
                Pumpkins are typically ready for harvest in the fall, around
                75-100 days after planting.
                <br /> Harvesting involves cutting the pumpkin from the vine,
                leaving a short stem attached.
              </p>
              <h3>8. Post-Harvest</h3>
              <p>
                Once harvested, pumpkins can be used for various purposes,
                including carving for decorations, cooking, or processing into
                pumpkin products.<br/> Some pumpkins are selected for seed-saving to
                be planted in the next growing season.
              </p>
              <h3>9. Decay and Decomposition:</h3>
              <p>
                If not consumed, pumpkins eventually decay and decompose.<br/> The
                decomposition process is influenced by factors such as
                temperature, moisture, and microbial activity.
              </p>
            </div>
          </Container>
        </div>
      </div>
    </div>
  );
}

export default Destination;
